@include breakpoint (small only) {
    
    html {
        font-size: 14px;
    }
    
    #header {
        padding: 10px;
        
        #logo-ope {
            background-size: 500px 500px;
            width: 160px; height: 30px;
            background-position: 0px -392px;
        }
    }
    
    .button {
        font-size: .8rem;
    }
    
    #temoins {
        & > .row {
            padding: 0px !important;
        }
    }
    
    #shake {
        background-size: 750px 750px;
        width: 150px; height: 93px;
        background-position: 0px -650px;
    }
    @keyframes shaking { from { background-position: 0px -487px} to { background-position: -300px -487px} }
    
    #slider {
        margin: 38px 20px 20px 20px !important;
    }
    
    #boulles {
        display: none;
    }
    
    #scene {
        margin-top: 75px;
    }

    .bulle {
        &:after {
            display: none;
        }
        top:25% !important;
        left:0 !important;
        right:0 !important;
        width: 90%;
        margin: auto;
        text-align: center;
    }
    
    #godown {
        margin-top: 30px;
        margin-left: 20px;
        
    }
    
    #don .give a {
        position: relative;
    }
    
}