
$max-layout-width   : 1420px;
$min-height-header  : 350px;

$black          : #2e121b;
$red            : #b31a30;
$beige1         : #e8e2d1;
$beige2         : #f1eee6;

body { 
    background-color: #fff;
    color:$black; 
    font-family: 'GothamBook', sans-serif; 
}


h1, h2, h3, h4, h5, h6 { font-family: 'Amatic SC', cursive; }
h1 { font-size: 2.5rem; line-height: 2.1rem; color:$red; font-weight: bold}
h2 { font-size: 1.75rem; line-height: 1.75rem; }
h3 { font-size: 1.2rem;}
h4 { font-size: 1.1rem;}
p, li { font-size: 1rem; line-height: 1.2rem; }
p {
    margin-bottom: 0;
    &:not(:last-child) {
        margin-bottom: 20px;
    }
    
}



//LAYOUT

.columns, .column { padding: 0 0; position: relative; }

.container { 
    max-width: $max-layout-width; margin: 20px auto 0 auto; padding: 0 40px;
}

#shake {
    background-size: 1000px 1000px;
    background-image: url('../images/sprite.svg');
    overflow: hidden; 
    width: 200px; height: 125px;
    background-position: 0px -650px;
    animation: shaking 200ms steps(2) infinite alternate;
    float: right
}
@keyframes shaking { from { background-position: 0px -650px} to { background-position: -400px -650px} }


#godown {
    background-size: 1000px 1000px;
    background-image: url('../images/sprite.svg');
    overflow: hidden; 
    width: 140px; height: 36px;
    background-position: -440px -650px;
    animation: godowning 1s infinite alternate;
    margin-top: 45px;
    transform-origin: center center;
    transition: 600ms transform cubic-bezier(0.68, -0.55, 0.265, 1.05);
    cursor:  pointer; 
    &:hover {
        transform: scale(1.1, 1.1);
        animation: none;
    }
}
@keyframes godowning { from { opacity: 1; } to { opacity: .6; } }

#down {
    background-size: 1000px 1000px;
    background-image: url('../images/sprite.svg');
    overflow: hidden; 
    width: 32px; height: 32px;
    background-position: -400px -650px;
    animation: downing 400ms infinite alternate;
    margin: 20px auto;
}
@keyframes downing { from { background-position: -400px -650px} to { background-position: -400px -660px} }


#logo-ope {
    background-size: 1000px 1000px;
    background-image: url('../images/sprite.svg');
    overflow: hidden; 
    width: 320px; height: 60px;
    background-position: 0px -785px;
    margin: 0 auto;
}


.sprite {
    background-size: 1000px 1000px;
    background-image: url('../images/sprite.svg');
    overflow: hidden; 
    width: 10px; height: 10px;
    position: absolute; z-index: -1;
//    border: 1px solid red;
    
    &.sapin-1 {
        width: 400px; height:400px;
        background-size: 2000px 2000px;
        bottom: -65px; right: -50px;
    }
    
    &.eclat-1 {
        width: 200px; height:200px;
        background-size: 1000px 1000px;
        background-position: -200px 0;
        top: -55px; left: -50px;
    }
    
    &.sapin-21 {
        width: 300px; height:300px;
        background-size: 1500px 1500px;
        background-position: -600px 0;
        bottom: -45px; left: 80px;
    }
    &.sapin-22 {
        width: 300px; height:300px;
        background-size: 1500px 1500px;
        background-position: -900px 0;
        bottom: -15px; right: 80px;
    }
    
    &.eclat-31, &.eclat-32, &.eclat-33, &.eclat-34 {
        width: 240px; height:200px;
        background-size: 1000px 1000px;
        background-position: 0px -250px;
        top: -50px; left:0px;
    }
    &.eclat-32 {
        background-position: -240px -250px;
    }
    &.eclat-33 {
        background-position: -480px -250px;
    }
    &.eclat-34 {
        background-position: -720px -250px;
        left:-35%;
    }
    
    &.bulle-temoin-1, &.bulle-temoin-2, &.bulle-temoin-3 {
        width: 240px; height:200px;
        background-size: 1000px 1000px;
        top:0; left:0;
    }
    &.bulle-temoin-1 {
        background-position: -240px -450px;
//        top: -50px; right:-10px;
    }
    &.bulle-temoin-2 {
        background-position: -480px -450px;
    }
    &.bulle-temoin-3 {
        background-position: -720px -450px;
    }
    
    &.fb, &.tw, &.yt {
        width: 32px; height:32px;
        background-position: 0px -500px;
        position: relative; display: inline-block; z-index: auto;
        vertical-align: bottom;
        transform-origin: center center;
        transform: scale(.9);
        opacity: .8;
        transition-delay: 300ms all ease-out;
        &:hover {
            transform: scale(1);
            opacity: 1;
        }
    }
    &.tw {
        background-position: -32px -500px;
    }
    &.yt {
        background-position: -64px -500px;
    }
}


section { 
    position: relative;
    overflow: hidden;
    
    &.noise:after {
        content:''; width: 100%; height:100%; 
        position: absolute; top:0; left:0; z-index: 10;
        background-image: url('../images/noise.png');
        background-position: center center;
    }
    
    & > .row {
        padding: 60px 20px !important;
        position: relative;
        z-index: 20;
        margin: 0 !important auto;
        max-width:$max-layout-width; 
    }
    
    
}

.button {
    padding: 10px 20px;
    border: 1px solid $red;
    color:$beige1;
    font-size: 1rem;
    font-family: 'GothamBold';
    position: relative;
    z-index: 2;
    &:before {
        content:''; position: absolute; width: 100%; height: 100%; top:0; left:0; z-index: -1; display: inline-block;
        background-color: $red;
        transform-origin: bottom center;
        transition: 200ms all ease-out;
    }
    
    &:hover {
        color: $red;
        &:before {
            transform: scaleY(0);
        }
    }
    
    &.white {
        color:$red;
        border-color: #fff;
        &:before {
            background-color: #fff;
        }
        &:hover {
            color: #fff;
            &:before {
                transform: scaleY(0);
            }
        }
    }
}

#header {
    background-color: $beige2;
    padding: 20px 20px;
    position: fixed;
    z-index: 999;
    width: 100%;
    top:0;
    img {
        height: 60px;
    }
    
}
 

.bulle {
    position: absolute; z-index: 25;
        top:0; left:0; right:0; margin: auto;
        padding: 10px 20px 10px 20px;
    &.bg {
        background-color: #501017;
        color: $beige2;
    }
    opacity: 0;
    transition: 200ms opacity ease-out; 
    
    &.visible {
        opacity: 1;
    }

}

#welcome {
    background-color: #206370;
    padding-bottom: 100px;
    #punchline {
        width:100%; max-width: 520px; margin: auto;
        position: absolute; top:20px; left:0; right: 0; z-index: 888;
        text-shadow: #206370 0 0 4px;
//        border: 1px solid red;
        color:$beige1; font-size: 1em; line-height: 1.2rem;
        text-align: center;
        @include breakpoint (small only) {
            padding: 0 10px;
        }
    }
    #bg {
        width: 100%; height:40%; 
        position: absolute; bottom: 0;
        background-color: #11434e;
        z-index: 1;
    }
    #explain{
        position: absolute; z-index: 25;
        bottom:0; left:0; right:0; margin: auto;
        padding: 10px 20px 10px 20px;
    }
    #legend {
        color:$beige1;
        font-size: .9rem; line-height: 1rem;
        padding-top: 40px;
    }    
    #scene {
        position: relative;
        z-index: 20;
    }
    #slider { 
        margin: 60px 40px 40px 40px;
        background: $red !important;
        border: none !important;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        height:10px; 
        position: relative;
        
        .ui-state-default {
            background-color:#caac71 !important;
            border: none;
            border-radius: 0;
            height: 50px;
            width: 10px;
            outline: none;
            top:-20px !important;
            box-shadow: rgba(0,0,0,.4) -2px 2px 0px;
            cursor: pointer;
            transition: 333ms transform ease-out;
            transform-origin: center center;
        }
        
        &:hover .ui-state-default {
            transform: scaleX(1.4);
        }
        
        &:after {
            content:''; position: absolute; top:0; left:0; height: 100%;
            background-color: #caac71;
        }
        
        .marker {
            width:3px; height:30px;
            background-color: $red;
            position: absolute; top:-10px;
            &:nth-child(1) { left:1%; }
            &:nth-child(2) { left:33%; }
            &:nth-child(3) { left:66%; }
            &:nth-child(4) { left:99%; }
            
        }
       
    }

}
#intro {
    background-color: $beige1;
    @include breakpoint (medium up) {
        h1 { margin:20px 0 0 30px;}
    }
}

#temoins {
    background-color: $beige2;
    padding-top: 40px;
    h1 {
        
        padding: 50px 0 0 50px;
    }
    h2 {
        font-family: "GothamBook";
        font-style: italic;
        font-size: .85em; line-height: 1rem;
        color:$black;
        padding: 0px 0 0 50px
    }
    .temoin-title {
        position: absolute;
        z-index: 22;
        right: -30px;
        top:-100px;
        &.left {
            right:0
        }
        @include breakpoint(small only) {
            position: relative;
            top: 0; right:0;
            h1 {
                padding-top: 0;
            }
        }
    }
    .temoin-text {
        padding: 80px 40px 20px 20px;
        @include breakpoint(small only) {
            padding-top: 0;
        }
    }
    
    .full {
        display: none;
    }
    
    a {
        color:#1c606e;
        font-weight: bold;
        position: relative;
        
        &:before {
            content:'> '
        }
        &:after {
            content:''; width: 100%; height: 2px; 
            position: absolute; bottom:-2px; left:0;
            background-color: #1c606e;
            transform: scaleX(0);
            transform-origin: left center;
            transition: 200ms all ease-out;
        }
        &:hover:after {
            transform: scaleX(1);
        }
    }
    
    
}

#xmas {
    background-color: #1e626e;
    color:#fff;
}

#don {
    background-color: $red;
    color:$beige1;
    & > .row:first-child { padding-bottom: 40px !important; }
    & > .row:last-child { padding-top: 0 !important; }
    h2 { 
        font-weight: normal; 
        margin-bottom: 20px;
        b {
            font-size: 2rem !important;
        }
    }
    h3 {
        font-family: 'GothamBold'; font-size: 5rem; line-height: 1;
