        font-size: .5rem; font-weight: bold;
    }
    h2 { font-size: 2rem; color:$red; font-weight: bold; padding-top: 30px;}
}

#benevole {
    background-color: #1d616e;
    color:#fff;
    h2 {
        font-size: 3rem; line-height: 3rem;
    }
    a {
        font-family: 'Amatic SC', cursive; 
