#scene {
    svg {
        overflow: hidden;
        height : 100%;
        width: 100%;
        position: absolute; top:0; left:0; right:0; margin: auto;
//        border:1px solid red;
    }
    #boulles > g {
        transform: translateY(-400px);
        transition: 600ms transform cubic-bezier(0.68, -0.55, 0.265, 1.05);
        &.ready {
            transform: translateY(0);
        }
    }
    #snow > g {
        transition: 800ms opacity ease-out; 
        @for $i from 1 through 6 {
            &:nth-child(#{$i}) {
                animation: shine 500ms*$i infinite alternate;
            }
        }

        @keyframes shine { from { opacity: 1} to { opacity: .5} }
    }
    #step1 {
        & > g {
            opacity: 0;
            transform-origin: bottom center;
            transition: 400ms transform cubic-bezier(0.68, -0.55, 0.265, 1.55), 200ms opacity ease-out;
            transform: scaleY(0) rotate(45deg);
            
            &:nth-child(1),
            &:nth-child(3),
            &:nth-child(5),
            &:nth-child(8) {
                transform: scaleY(0) rotate(-45deg);
            }
            &.ready {
                transform: scaleY(1) rotate(0);
                opacity: 1;
            }
        }
    }
    #step2, #step3, #step4 {
        & > g { 
            opacity: 0;
            transition: 300ms opacity ease-out, 500ms transform ease-out;
            &.visible {
                opacity: 1;
                transform: scaleY(1);
            }
        }
    }
    //venant de la gauche
        #perso21, #perso41_1_ {
            transform: translateX(-30px);
            &.visible {
                transform: translateX(0);
            }
        }
    //venant de la droite
        #perso32 {
            transform: translateX(30px);
            &.visible {
                transform: translateX(0);
            }
        }
    //
    
}


//ANIMATE
.animate { position: relative;  }
.animate--from-left { opacity: 0; transform: translate(-20px, 0); transition: 300ms all ease-out 250ms; 
    &.done { opacity: 1; transform: translate(0, 0); }
}

.animate--from-right { opacity: 0; transform: translate(20px, 0); transition: 300ms all ease-out 250ms; 
    &.done { opacity: 1; transform: translate(0, 0); }
}

.animate--from-bottom { opacity: 0; transform: translate(0, 20px); transition: 300ms all ease-out 250ms; 
    &.done { opacity: 1; transform: translate(0, 0); }
}

.animate--from-top { opacity: 0; transform: translate(0, -20px); transition: 300ms all ease-out 250ms; 
    &.done { opacity: 1; transform: translate(0, 0); }
}

.animate--opacity { opacity: 0; transition: 300ms all ease-out 250ms;
    &.done { opacity: 1;}
}

.animate--delay-500 { transition-delay: 500ms !important;}
.animate--delay-750 { transition-delay: 750ms !important;}
.animate--delay-1000 { transition-delay: 1000ms !important;}
.animate--delay-1250 { transition-delay: 1250ms !important;}
.animate--delay-1500 { transition-delay: 1500ms !important;}
.animate--delay-1750 { transition-delay: 1500ms !important;}
.animate--delay-2000 { transition-delay: 1500ms !important;}

.animate--hide { opacity: 0; transition: 200ms all ease-in 0; }





